<template>
  <div class="default-margin">
    <v-card class="default-padding">
      <v-btn @click.native="signUp">注册</v-btn>
    </v-card>
    <v-card class="default-padding" style="margin-top: 12px;">
      <v-form>
        <v-row>
          <v-col cols="12" sm="3" md="2">
            <v-text-field
              v-model="query.name"
              placeholder="用户名"
              clearable
              dense
              solo
              :hide-details="true"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="3" md="2">
            <v-text-field
              v-model="query.phone"
              placeholder="手机"
              clearable
              dense
              solo
              :hide-details="true"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="4" md="2">
            <v-btn @click.native="changePage(1)">查询</v-btn>
          </v-col>
        </v-row>
      </v-form>
    </v-card>
    <v-card style="margin-top: 12px;">
      <v-data-table
        :headers="tableHeader"
        :items="list"
        :loading="loading"
        calculate-widths
        disable-pagination
        disable-filtering
        disable-sort
        hide-default-footer
      >
        <template v-slot:item.name="{ item }">
          <a :href="`${$config.domain}/user/` + item.id" target="_blank">
            {{ item.name }}
          </a>
        </template>
        <template v-slot:item.head="{ item }">
          <a :href="`${$config.domain}/user/` + item.id" target="_blank">
            <img
              v-lazy="$img.head(item.head)"
              style="width: 120px;height: 120px;margin: 10px 0;"
            />
          </a>
        </template>
      </v-data-table>
      <v-pagination
        v-model="pageable.page"
        :length="totalPages"
        :total-visible="7"
        @input="changePage"
      ></v-pagination>
    </v-card>
  </div>
</template>

<script>
import { Pageable } from "../../assets/script/model"
import { QueryForm } from "./script/model"
import qs from "qs"
import { mapActions } from "vuex"
import { tableHeadersConstant } from "./script/constant"

export default {
  data() {
    return {
      pageable: new Pageable(),
      query: null,
      tableHeader: tableHeadersConstant(),
      loading: false,
      list: [],
      totalPages: 0
    }
  },
  watch: {
    $route: {
      handler(to) {
        this.pageable.page = parseInt(to.params.page) || 1
        this.pageable.sort = "createDate,desc"
        this.query = new QueryForm(to.query)
        this.paging()
      },
      immediate: true
    }
  },
  methods: {
    ...mapActions("user", ["APaging", "ASignUp"]),
    changePage(page) {
      let query = Object.assign({}, this.query)
      query.date = query.date ? query.date.join(",") : []
      this.$router.push(`/user/${page}?${qs.stringify(query)}`)
    },
    async paging() {
      this.loading = true
      this.$vuetify.goTo("#app")
      let paging = (
        await this.APaging(Object.assign({}, this.pageable, this.query))
      ).data
      this.loading = false
      this.list = paging.content || []
      this.totalPages = paging.totalPages || 0
      document.getElementsByTagName("main")[0].scrollTo(0, 0)
    },
    async signUp() {
      try {
        let { input } = await this.$prompt({
          placeholder: "请输入所谓的手机号"
        })
        let result = await this.ASignUp({ phone: input })
        if (result.status === 200) {
          this.$notify({ text: "成功", color: "success" })
          this.getList()
        } else {
          this.$notify({ text: result.message, color: "error" })
        }
        // eslint-disable-next-line no-empty
      } catch (e) {}
    }
  }
}
</script>

<style type="text/less" lang="less" scoped></style>
